.Navigation-container {
  padding: 20px;
  color: white;
  text-align: center;
}

.Navigation-container h2 {
  margin: 5px;
}

.Navigation-container h4 {
  margin: 10px;
}

.Navigation-logo {
  -webkit-animation: Navigation-logo-spin infinite 20s linear;
          animation: Navigation-logo-spin infinite 20s linear;
  height: 80px;
}

@-webkit-keyframes Navigation-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes Navigation-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



